import React from "react";
import Section from "./section.component";

function FlashSales() {
  return (
    <Section title="Flash Sale">
      <div
        className="grid gap-2 bg-white"
        style={{ gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))" }}
      >
        {flashSalesData.map((data, index) => (
          <div key={index} className="p-2 hover:shadow-lg cursor-pointer">
            <img
              src={data.image}
              alt={data.title}
              className="h-48 mx-auto object-cover"
            />
            <div className="grid grid-flow-row pb-6 ">
              <p className="h-12 overflow-hidden ">{data.title}</p>
              <p className="text-lg text-red-400 pt-1">Rs.{data.price}</p>
              <p>
                <span className="text-gray-400 line-through">
                  Rs.{data.price}
                </span>
                <span className="text-sm px-2">-30%</span>
              </p>
            </div>
          </div>
        ))}
      </div>
    </Section>
  );
}

